.card {
    width: 350px;
    height: 235px;
    background-color: #4C49ED;
    padding: 24px;
}

.card_balance {
    display: flex;
    flex-direction: column;
    font-size: 20px;
    color: #fff;
    font-weight: 600;
}

.card_balance_title {
    font-size: 12px;
    color: #fff;
}

.card_holder {
    display: flex;
    flex-direction: column;
    padding-top: 33px;
}

.card_holder_title {
    font-size: 12px;
    color: #fff;
}

.card_holder_name {
    font-size: 15px;
    font-weight: 600;
    color: #fff;
}

.card_num::before {
    display: block;
    margin-bottom: 20px;
    content: '';
    height: 1px;
    width: 100%;
    background-color: #000;
}

.card_num {
    font-size: 22px;
    color: #fff;
    margin-top: 35px;
    padding-top: 20px;
}